html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  font-size: 1rem;
  font-weight: 300;
  text-decoration: none;
}

h1 {
  font-size: 3rem;
  font-weight: 500;
}

h2 {
  font-size: 2.1875rem;
  font-weight: 400;
}

h3 {
  font-size: 1.25rem;
  font-weight: 500;
}

p {
  font-size: 1rem;
  font-weight: 300;
}